<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/nucleicacid"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="nucleicAcidRemove"></Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="120">
         <FormItem label="">
            <Input v-model="editForm.CreateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LatestupdateDate" disabled  placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="NucleicAcidSGIID">
            <Input v-model="editForm.NucleicAcidID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="BiospeciemensID">
            <Input v-model="editForm.BiospeciemensID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="NucleicAcidSGIID">
            <Input v-model="editForm.NucleicAcidSGIID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="NAExtractionDate">
            <DatePicker @on-change="changeNAExtractionDate" show-week-numbers placement="bottom-start" placeholder="" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="" prop="NAExtractionManipulatorID">
            <Select v-model="editForm.NAExtractionManipulatorID" placeholder="" style="width:280px">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.NAExtractionAmount" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.NAExtractionUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.NAVolume" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.NAVolumeUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.NAConc" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.NAConcUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.NAAmount" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.NAAmountUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Select v-model="editForm.NAQuality" placeholder="" style="width: 280px">
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
            </Select>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.NAType" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Select v-model="editForm.Status" placeholder="" style="width: 280px">
              <Option value="1">1</Option>
              <Option value="2">2</Option>
            </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('editForm')"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/datainterface/getEmployeeName.php").then(response=>{
        // console.log(response.data);
        this.employeeData = response.data
        // this.reviewerData = response.data
      })
      this.$Common.list(this,"/php/nucleicacids/index.php",{NucleicAcidID:this.nucleicAcidID},(response)=>{
        this.editForm = response.data.data[0]
      })
    },
    data() {
      return {
        nucleicAcidID:location.search.split('=')[1],
        employeeData:[],
        editForm:{},
        rule:{
          BiospeciemensID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          NucleicAcidSGIID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          NAExtractionManipulatorID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          NAExtractionDate:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      nucleicAcidRemove(){
        // console.log(id);
        this.$Common.remove(this,"/php/nucleicacids/del.php",{NucleicAcidID:[this.nucleicAcidID]},()=>{
          setTimeout(()=>{
            history.back();
          },1000)
        })  
      },
      changeNAExtractionDate(date){
        this.addForm.NAExtractionDate = date
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            this.$Common.edit(this,"/php/nucleicacids/edit.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


