<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/user"></BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='browuser'"></BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='edituser'"></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning"></Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="">
            <Input v-model="editForm.CreateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LatestupdateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="email">
            <Input v-model="editForm.email" :disabled="currentPath==='browuser'" placeholder="" style="width: 280px"></Input>
            <Button type="text" size="large" class="resetPwdBtn" @click="changePwd"></Button>
            <Button type="text" size="large" class="resetPwdBtn" @click="resetPwd">root123456</Button>
          </FormItem>
          <FormItem label="LastName" prop="LastName">
            <Input v-model="editForm.LastName" :disabled="currentPath==='browuser'" placeholder="LastName" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="MiddleName">
            <Input v-model="editForm.MiddleName" :disabled="currentPath==='browuser'" placeholder="MiddleName" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="FirstName" prop="FirstName">
            <Input v-model="editForm.FirstName" :disabled="currentPath==='browuser'" placeholder="FirstName" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="authority">
            <Select v-model="editForm.authority" :disabled="currentPath==='browuser'" placeholder="" style="width: 280px">
              <Option value="1111">1111</Option>
              <Option value="2222">2222</Option>
            </Select>
          </FormItem>
          <FormItem label="" prop="status">
            <Select v-model="editForm.status" :disabled="currentPath==='browuser'" placeholder="" style="width: 280px">
              <Option value="1111"></Option>
              <Option value="2222"></Option>
            </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" v-if="currentPath==='browuser'" @click="handleEdit"></Button>
              <Button type="primary" v-if="currentPath==='edituser'" @click="handleSubmit('editForm')"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
    },
    data() {
      return {
        currentPath:location.pathname.split("/")[1],
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          CreateDate:'111',
          LatestupdateDate:'111',
          email:'111',
          LastName:'111',
          MiddleName:'111',
          FirstName:'111',
          authority:'1111',
          status:'1111'
        },
        rule:{
          email:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          LastName:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          FirstName:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          authority:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          status:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
        }
      }
    },
    methods: {
      handleEdit(){
        this.currentPath="edituser"
      },
      changePwd(){
        this.$router.push("/changepwd")
      },
      resetPwd(){
        //123456

      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push("/user")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
    .resetPwdBtn{
      color:@themeColor;
      &:hover{
        text-decoration:underline;
      }
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


