<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library"></BreadcrumbItem>
      <BreadcrumbItem :to="'/browlibrary' + libID"></BreadcrumbItem>
      <BreadcrumbItem>QC</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>QC</p>
        <Button type="primary"></Button>
        <Button></Button>
      </div>
      <div class="singleAddContainer">
        <p>QC</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="" prop="LibID">
            <Input v-model="addForm.LibID" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="TotalBases">
            <Input v-model="addForm.TotalBases" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="TotalReadNumber">
            <Input v-model="addForm.TotalReadNumber" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="DuplicationRate">
            <Input v-model="addForm.DuplicationRate" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="MappingRate">
              <Input v-model="addForm.MappingRate" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="ReadLength">
              <Input v-model="addForm.ReadLength" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="CaptureEfficacy">
              <Input v-model="addForm.CaptureEfficacy" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="ProbeMeanDepth">
              <Input v-model="addForm.ProbeMeanDepth" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="ProbeMedianDepth">
            <Input v-model="addForm.ProbeMedianDepth" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="ProbeUniformity">
            <Input v-model="addForm.ProbeUniformity" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="DepthGT1Rate">
            <Input v-model="addForm.DepthGT1Rate" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="30" prop="DepthGT30Rate">
            <Input v-model="addForm.DepthGT30Rate" placeholder="30" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Select v-model="addForm.Status" placeholder="" style="width: 280px">
              <Option value="1">1</Option>
              <Option value="2">2</Option>
            </Select>
        </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        libID:location.search.split('=')[1],
        addForm: {
          LibID:location.search.split('=')[1],
          TotalBases:'111111',
          TotalReadNumber:'1000',
          DuplicationRate:'10',
          MappingRate:'10',
          ReadLength:'10',
          CaptureEfficacy:'10',
          ProbeMeanDepth:'10',
          ProbeMedianDepth:'10',
          ProbeUniformity:'10',
          DepthGT1Rate:'10',
          DepthGT30Rate:'5',
          Status:'1'
        },
        rule:{
          LibID:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          TotalBases:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          TotalReadNumber:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          DuplicationRate:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          MappingRate:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          ReadLength:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          CaptureEfficacy:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          ProbeMeanDepth:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          ProbeMedianDepth:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          ProbeUniformity:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          DepthGT1Rate:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          DepthGT30Rate:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            this.$Common.add(this,"/php/qc/add.php",obj,()=>{
              history.back()
            })
          }
        })
      },
      backList(){
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


