<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="libRemove"></Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="140">
          <FormItem label="">
            <Input v-model="editForm.CreateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="LibID">
            <Input v-model="editForm.LibID" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="NucleicAcidID">
            <Input v-model="editForm.NucleicAcidID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="LibSGIID">
            <Input v-model="editForm.LibSGIID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label=""  prop="LibConsManipulatorID">
            <Select v-model="editForm.LibConsManipulatorID" placeholder="" style="width:280px">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="" prop="LibConsDate">
            <DatePicker type="date" @on-change="changeLibConsDate" :value="editForm.LibConsDate" show-week-numbers placement="bottom-start" placeholder="" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LibConsAmount" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LibConsUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.LibVolume" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.LibVolumeUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.LibConc" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.LibConcUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LibAmount" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LibAmountUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LibInsSize" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LibInsSizeUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Select v-model="editForm.LibQuality" placeholder="" style="width: 280px">
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
            </Select>
          </FormItem>
          <FormItem label="">
            <Select v-model="editForm.Status" placeholder="" style="width: 280px">
              <Option value="1">1</Option>
              <Option value="2">2</Option>
            </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('editForm')"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/datainterface/getEmployeeName.php").then(response=>{
        // console.log(response.data);
        this.employeeData = response.data
      })
      this.$Common.list(this,"/php/library/edit.php",{LibID:this.libID},(response)=>{
        this.editForm = response.data.data[0]
      })
    },
    data() {
      return {
        libID:location.search.split("=")[1],
        employeeData:[],
        editForm:{},
        rule:{
          NucleicAcidID:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          LibSGIID:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          LibConsManipulatorID:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          LibConsDate:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      libRemove(){
        this.$Common.remove(this,"/php/library/del.php",{LibID:[this.libID]},()=>{
          setTimeout(()=>{
            history.back();
          },1000)
        })
      },
      changeLibConsDate(date){
        this.editForm.LibConsDate = date
      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            console.log(obj);
            this.$Common.edit(this,"/php/library/update.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


