<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/subject"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="subjectRemove"></Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="">
            <Input v-model="editForm.CreateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="LastName">
            <Input v-model="editForm.LastName" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.MiddleName" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="FirstName">
            <Input v-model="editForm.FirstName" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="DateOfBirth">
            <DatePicker @on-change="handleChange" :value="editForm.DateOfBirth" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="" prop="Gender">
            <RadioGroup v-model="editForm.Gender" placeholder="" style="width: 280px">
              <Radio label="m"></Radio>
              <Radio label="f"></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="" prop="IDType">
            <RadioGroup v-model="editForm.IDType" placeholder="" style="width: 280px">
              <Radio label=""></Radio>
              <Radio label=""></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="" prop="IDNumber">
            <Input v-model="editForm.IDNumber" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="PhoneNumber">
            <Input v-model="editForm.PhoneNumber" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.Efront" placeholder="" style="width: 280px">
              <Select v-model="editForm.Eback" slot="append"  placeholder="" style="width: 120px">
                <Option value="@163.com">@163.com</Option>
                <Option value="@qq.com">@qq.com</Option>
                <Option value="@sina.com">@sina.com</Option>
              </Select>
            </Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.familyHistory" type="textarea" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')"></Button>
            <Button @click="backList"></Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$Common.list(this,"/php/subject/index.php",{SubjectID:this.subjectID},(response)=>{
        this.editForm = response.data.data[0]
        if(this.editForm.Email.trim()){
          this.editForm.Efront=this.editForm.Email.split("@")[0]
          this.editForm.Eback="@" + this.editForm.Email.split("@")[1]
        }else{
          this.editForm.Efront=''
          this.editForm.Eback="@163.com"
        }
      })
    },
    data() {
      return {
        subjectID:location.search.split("=")[1],
         DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{},
        rule:{
          LastName:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          FirstName:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          DateOfBirth:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          Gender:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          IDType:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          IDNumber:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          PhoneNumber:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      subjectRemove(){
        // console.log(id);
        this.$Common.remove(this,"/php/subject/del.php",{SubjectID:[this.subjectID]},()=>{
          setTimeout(()=>{
            // history.back()
            this.$router.push("/subject")
          },1000)
        })
      },
      handleChange(date){
        // console.log(date);
        this.editForm.DateOfBirth = date;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            //Email
            if(!this.editForm.Efront.trim()&&!this.editForm.Eback.trim()){
              this.editForm.Email = this.editForm.Efront.trim()+this.editForm.Eback.trim();
            }
            this.editForm.Age = String(this.editForm.Age)
            let obj = {};
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            this.$Common.edit(this,"/php/subject/edit.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


