<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library"></BreadcrumbItem>
      <BreadcrumbItem :to="'/browlibrary' + editForm.LibID"></BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='browvariant'">QC</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='editvariant'">QC</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="variantRemove"></Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="">
            <Input v-model="editForm.CreateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="LibID">
            <Input v-model="editForm.LibID" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="VariantID">
            <Input v-model="editForm.VariantID" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="ChrName">
            <Select v-model="editForm.ChrName" :disabled="currentPath=='browvariant'" placeholder="" style="width: 280px">
              <Option v-for="(item,index) in chrArr":key="index" :value="item.ChrName">{{item.ChrName}}</Option>
            </Select>
          </FormItem>
          <FormItem label="" prop="Position">
            <Input v-model="editForm.Position" :disabled="currentPath=='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Gene Symbol" prop="Symbol">
            <Input v-model="editForm.Symbol" :disabled="currentPath=='browvariant'" placeholder="Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.transcript" :disabled="currentPath=='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="REF">
            <Input v-model="editForm.Ref" :disabled="currentPath=='browvariant'" placeholder="REF" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ALT">
            <Input v-model="editForm.Alt" :disabled="currentPath=='browvariant'" placeholder="ALT" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Select v-model="editForm.VariantType" :disabled="currentPath=='browvariant'" placeholder="" style="width: 280px">
              <Option v-for="(item,index) in variantTypeArr":key="index" :value="item.VariantTypeID">{{item.VariantType}}</Option>
            </Select>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.Genotype" :disabled="currentPath=='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.MinorAlleleFraction" :disabled="currentPath=='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.DepthOfCoverage" :disabled="currentPath=='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.Consequence" :disabled="currentPath=='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="editForm.exon_intron" :disabled="currentPath=='browvariant'" placeholder="exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="editForm.Location" :disabled="currentPath=='browvariant'" placeholder="exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSp">
              <Input v-model="editForm.HGVSp" :disabled="currentPath=='browvariant'" placeholder="HGVSp" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="editForm.HGVSc" :disabled="currentPath=='browvariant'" placeholder="HGVSc" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="rsID">
            <Input v-model="editForm.rsID" :disabled="currentPath=='browvariant'" placeholder="rsID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.Clin_Sig" :disabled="currentPath=='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.Flag" :disabled="currentPath=='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.Impact" :disabled="currentPath=='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit" v-if="currentPath=='browvariant'"></Button>
              <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath=='editvariant'"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$Common.list(this,"/php/datainterface/getVariantByType.php",{},(response)=>{
        this.variantTypeArr = response.data
      })
      this.$Common.list(this,"/php/datainterface/getChr.php",{},(response)=>{
        this.chrArr = response.data
      })
      this.$Common.list(this,"/php/snp_indel/edit.php",{VariantID:this.variantID},(response)=>{
        this.editForm = response.data.data[0];
      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[1],
        variantID:location.search.split('=')[1],
        variantTypeArr:[],
        chrArr:[],        
        editForm:{},
        rule:{
          LibID:[
            {
              required: true, message: '', trigger: 'blur,change,change'
            }
          ],
          ChrName:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          Position:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          Symbol:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      variantRemove(){
        this.$Common.remove(this,"/php/snp_indel/del.php",{VariantID:[this.variantID]},()=>{
          history.back()
        })
      },
      handleEdit(){
        this.currentPath="editvariant"
      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            // console.log(obj);
            this.$Common.edit(this,"/php/snp_indel/update.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


