import Vue from 'vue'
import axios from 'axios'
Vue.prototype.$http = axios
var qs = require('qs')
const Common = {}

Common.send = function(that,url,obj,callback){
  that.$http.post(url, qs.stringify(obj)).then(response => {
    console.log(response.data);
    callback(response)
  })
}

/* 
  
    that: 
    url: 
    obj: 
    callback: 
*/
Common.list = function(that,url,obj,callback){
  that.$http.post(url, qs.stringify(obj)).then(response => {
    console.log(response.data);
    callback(response)
  })
}

/* 
  
    that: 
    url: 
    obj: 
    callback: 
*/
Common.add = function(that,url,obj,callback){
  that.$http.post(url, qs.stringify(obj)).then(response => {
    console.log(response.data);
    if(response.data.code===0){
      that.$Message.success('...')
      setTimeout(()=>{
        callback()
      },1000)
    }else{
      that.$Message.error('' + response.data.info)
    }
    
  })
}

/* 
  
    that: 
    url: 
    obj: 
    callback: 
*/
Common.edit = function(that,url,obj,callback){
  that.$http.post(url, qs.stringify(obj)).then(response => {
    console.log(response.data);
    if(response.data.code===0){
      that.$Message.success('...')
      setTimeout(()=>{
        callback()
      },1000)
    }else{
      that.$Message.error('' + response.data.info)
    }
    
  })
}

/* 
  
    that: 
    url: 
    obj: 
    callback: 
*/
Common.remove = function(that, url, obj, callback) {
  that.$Modal.confirm({
    title: '',
    content: '',
    onOk: () => {
      that.$http.post(url, qs.stringify(obj)).then(response => {
        // console.log(response.data);
        if (response.data.code === 0) {
          that.$Message.success('...')
          callback()
        } else {
          that.$Message.error('' + response.data.info)
        }
      })
    }
  })
}

/* 
  
    that: 
    IDS: 
    url: s
    obj: 
    callback: 
*/
Common.batchRemove = function(that, IDS, url, obj, callback) {
  if (IDS.length == 0) {
    that.$Message.warning('')
  } else {
    that.$Modal.confirm({
      title: '',
      content: '',
      onOk: () => {
        that.$http.post(url, qs.stringify(obj)).then(response => {
          // console.log(response.data);
          if (response.data.code === 0) {
            that.$Message.success('...')
            callback()
          } else {
            that.$Message.error('' + response.data.info)
          }
        })
      }
    })
  }
}

export default Common



