<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/panel">Panel</BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="removePanel"></Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="">
            <Input v-model="editForm.CreateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel" prop="ProjectID">
            <Input v-model="editForm.ProjectID" disabled placeholder="Panel" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel" prop="PanelName_cn">
            <Input v-model="editForm.PanelName_cn" placeholder="Panel" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel" prop="PanelName_en">
            <Input v-model="editForm.PanelName_en" placeholder="Panel" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')"></Button>
            <Button @click="backList"></Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/panel/index.php",qs.stringify({PanelID:this.panelID})).then(response=>{
        // console.log(response.data);
        this.editForm = response.data.data[0]
      })
    },
    data() {
      return {
        panelID:location.search.split('=')[1],
        editForm:{},
        rule:{
          ProjectID:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          PanelName_cn:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          PanelName_en:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      removePanel(){
        this.$http.post("/php/panel/del.php",qs.stringify({PanelID:[this.panelID]})).then(response=>{
          if(response.data){
            this.$Message.success('...')
            setTimeout(()=>{
              this.$router.push("/panel")
            },1000)
          }else{
            this.$Message.success('...')
          }
        })
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              obj[key]=this.editForm[key].trim()
            }
            this.$Common.edit(this,"/php/panel/edit.php",obj,()=>{
              this.$router.push("/panel")
            })
          }
        })
      },
      backList(){
        this.$router.push("/panel")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


