<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/subject"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p></p>
        <Button type="primary"></Button>
        <Button></Button>
        <!--<a class="ivu-btn ivu-btn-primary" href="http://www-dev.basicsmanage.com/php/modal.xlsx" download="filename"></a>
        <Upload action="" :before-upload="handleUpload">
          <Button type="primary"></Button>
        </Upload>
        <div v-if="file !== null">Upload file: {{ file.name }} 
          <Button type="text" @click="upload" :loading="loadingStatus">{{ loadingStatus ? '' : '' }}
          </Button>
        </div>-->
      </div>
      <div class="singleAddContainer">
        <p></p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="" prop="LastName">
            <Input v-model="addForm.LastName" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.MiddleName" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="FirstName">
            <Input v-model="addForm.FirstName" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="DateOfBirth">
            <DatePicker @on-change="handleChange" format="yyyy-MM-dd" :options="DataPickerOption" placement="bottom-start" placeholder="" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="" prop="Gender">
            <RadioGroup v-model="addForm.Gender" placeholder="" style="width: 280px">
              <Radio label="m"></Radio>
              <Radio label="f"></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="" prop="IDType">
            <RadioGroup v-model="addForm.IDType" placeholder="" style="width: 280px">
              <Radio label=""></Radio>
              <Radio label=""></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="" prop="IDNumber">
            <Input v-model="addForm.IDNumber" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="PhoneNumber">
            <Input v-model="addForm.PhoneNumber" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.Efront" placeholder="" style="width: 280px">
              <Select v-model="addForm.Eback" slot="append"  placeholder="" style="width: 120px">
                <Option value="@163.com">@163.com</Option>
                <Option value="@qq.com">@qq.com</Option>
                <Option value="@sina.com">@sina.com</Option>
              </Select>
            </Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.FamilyHistory" type="textarea" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')"></Button>
            <Button @click="backList"></Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        // file: null,
        // loadingStatus: false,
        addForm: {
          LastName:'',
          MiddleName:'',
          FirstName:'',
          DateOfBirth:'',
          Gender:'m',
          IDType:'',
          IDNumber:'',
          PhoneNumber:'',
          Efront:'',
          Eback:'@163.com',
          Email:'',
          FamilyHistory:''
        },
        rule:{
          LastName:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          FirstName:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          DateOfBirth:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          Gender:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          IDType:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          IDNumber:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          PhoneNumber:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      // download(){
      //   window.open("http://www-dev.basicsmanage.com/php/modal.xls","_blank")
      // },
      // handleUpload (file) {
      //   this.file = file;
      //   return false;
      // },
      // upload () {
      //   this.loadingStatus = true;
      //   setTimeout(() => {
      //     this.file = null;
      //     this.loadingStatus = false;
      //     this.$Message.success('Success')
      //   }, 1500);
      // },
      handleChange(date){
        // console.log(date);
        this.addForm.DateOfBirth = date;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            //Email
            if(this.addForm.Efront.trim()!==''){
              this.addForm.Email = this.addForm.Efront.trim()+this.addForm.Eback.trim();
            }
            let obj = {};
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            this.$Common.add(this,"/php/subject/add.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
    .ivu-upload{
      display:inline-block;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


