<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library"></BreadcrumbItem>
      <BreadcrumbItem :to="'/browlibrary' + libID"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p></p>
        <Button type="primary"></Button>
        <Button></Button>
      </div>
      <div class="singleAddContainer">
        <p></p>
        <Form ref="addForm" :inline="true" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="" prop="LibID">
            <Input v-model="addForm.LibID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="ChrName">
            <Select v-model="addForm.ChrName" placeholder="" style="width: 280px">
              <Option v-for="(item,index) in chrArr":key="index" :value="item.ChrName">{{item.ChrName}}</Option>
            </Select>
          </FormItem>
          <FormItem label="" prop="Position">
            <Input v-model="addForm.Position" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Gene Symbol" prop="Symbol">
            <Input v-model="addForm.Symbol" placeholder="Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.transcript" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="REF">
            <Input v-model="addForm.Ref" placeholder="REF" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ALT">
            <Input v-model="addForm.Alt" placeholder="ALT" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Select v-model="addForm.VariantType" placeholder="" style="width: 280px">
              <Option v-for="(item,index) in variantTypeArr":key="index" :value="item.VariantTypeID">{{item.VariantType}}</Option>
            </Select>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.Genotype" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.MinorAlleleFraction" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.DepthOfCoverage" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.Consequence" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="addForm.exon_intron" placeholder="exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="addForm.Location" placeholder="exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSp">
              <Input v-model="addForm.HGVSp" placeholder="HGVSp" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="addForm.HGVSc" placeholder="HGVSc" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="rsID">
            <Input v-model="addForm.rsID" placeholder="rsID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.Clin_Sig" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.Flag" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.Impact" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$Common.list(this,"/php/datainterface/getVariantByType.php",{},(response)=>{
        this.variantTypeArr = response.data
      })
      this.$Common.list(this,"/php/datainterface/getChr.php",{},(response)=>{
        this.chrArr = response.data
      })
    },
    data() {
      return {
        libID:location.search.split('=')[1],
        // symbolArr:[],
        // symbolData:[],
        variantTypeArr:[],
        chrArr:[],
        addForm: {
          LibID:location.search.split('=')[1],
          ChrName:'chr1',
          Position:'111',
          Symbol:'ABCB1',
          transcript:'111',
          Ref:'111',
          Alt:'111',
          VariantType:'1',
          Genotype:'111',
          MinorAlleleFraction:'111',
          DepthOfCoverage:'111',
          Consequence:'111',
          exon_intron:'111',
          Location:'111',
          HGVSp:'111',
          HGVSc:'111',
          rsID:'111',
          Clin_Sig:'111',
          Flag:'111',
          Impact:'111'
        },
        rule:{
          LibID:[
            {
              required: true, message: '', trigger: 'blur,change,change'
            }
          ],
          ChrName:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          Position:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          Symbol:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            this.$Common.add(this,"/php/snp_indel/add.php",obj,()=>{
              history.back()
            })
          }
        })
      },
      backList(){
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


